:root {
  --background: #0b0d0f;
  --foreground: #e6e7e9;
  --muted: #111317;
  --muted-foreground: #a1a6ad;
  --brand: #ff7b00;
  --brand-foreground: #0b0d0f;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0b0d0f;
    --foreground: #e6e7e9;
  }
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-vyom-sans), ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, "Helvetica Neue", Arial, "Apple Color Emoji", "Segoe UI Emoji";
}
.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem;
  padding-right: 1rem;
}
